___CSS_LOADER_EXPORT___.push([module.id, `/* Container styles */
.addproduct-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: #f9f9f9;
  padding: 1rem;
}

/* Card styles */
.addproduct-card {
  background: white;
  padding: 2rem;
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 600px;
  text-align: center;
}

/* Title styles */
.addproduct-title {
  font-size: 1.8rem;
  margin-bottom: 1.5rem;
  color: #333;
}

/* Error message */
.addproduct-error {
  color: red;
  font-size: 0.9rem;
  margin-bottom: 1rem;
}

/* Form styles */
.addproduct-form {
  display: flex;
  flex-direction: column;
}

.form-group {
  margin-bottom: 1rem;
  text-align: left;
}

label {
  font-size: 0.9rem;
  color: #555;
}

input,
textarea,
select {
  width: 100%;
  padding: 0.75rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 1rem;
}

input:focus,
textarea:focus,
select:focus {
  outline: none;
  border-color: #007bff;
  box-shadow: 0 0 4px rgba(0, 123, 255, 0.3);
}

textarea {
  resize: vertical;
  min-height: 100px;
}

.checkbox-group {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

input[type="checkbox"] {
  width: auto;
  margin: 0;
}

/* Button styles */
.addproduct-button {
  background: #007bff;
  color: white;
  border: none;
  padding: 0.75rem;
  border-radius: 4px;
  font-size: 1rem;
  cursor: pointer;
  margin-top: 1rem;
}

.addproduct-button:hover {
  background: #0056b3;
}

.reset-button {
  background: #6c757d;
  color: white;
  border: none;
  padding: 0.75rem;
  border-radius: 4px;
  font-size: 1rem;
  cursor: pointer;
  margin-top: 1rem;
}

.reset-button:hover {
  background: #5a6268;
}

/* Links */
.addproduct-links {
  margin-top: 1rem;
  text-align: center;
}

.back-link {
  color: #007bff;
  text-decoration: none;
}

.back-link:hover {
  text-decoration: underline;
}
